body {
  margin: 0;
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(34, 42, 38);
  text-shadow:  1px 1px 2px black;
  background-color: rgb(126, 164, 145);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-container {
  display: inline-flex;
  width: 100%;
  text-align: left;
  flex-direction: row;
  align-self: stretch;
}


div h3 {
  font-size: 20px;
  color: rgb(164, 213, 188);
  background-color: #333;
  padding: 20px;
  margin: 0 auto;
  height: 70px;
  width: 28%;
  border: 6px solid rgb(12, 13, 12);
}
.budget{
  font-size: 20px;
  color: rgb(164, 213, 188);
  /* background-color: #333; */
  /* padding: 20px; */
  margin: 0 auto;

  background-color: rgb(41, 65, 104);
  
}
#NewBudget{
  width:40%
}
.spent{
  background-color: rgb(87, 32, 32);
  
}
.updateBudget{
  
  color:rgb(200, 222, 201);
  background-color: rgb(50, 47, 47);
  justify-content: left;
  padding: 8px 20px;
  border-radius: 10%;
  
}

.saveButton{
  background-color: rgb(174, 213, 184);
  position: left;
}
div p {
  margin-top: 4em;
  margin-bottom: 7em;
}

div a {
  background-color: dodgerblue;
  padding: 15px 75px;
  border-radius: 2px;
  text-decoration: none;
}

.expense-table{
  width: 85%;
  align-self: center;
}
.expense_item{
  width: 100%;
  font-size: large;
  background-color: lightgray;

}
.rm_exp{
  color:white;
  background-color:rgb(122, 23, 23);
  /* position: absolute;
  right: 0; */
  padding: 4px 10px;
  border-radius: 50%;
}
.ExpenseList{
  color:black;
  text-shadow: none;
}
.expense-table, .th{
  border: 5px solid;
  table-layout: auto;
  width: 90%;
  margin: 3%;
  background-color: beige;
}

.th{
  font-weight: bolder;
  font-size: larger;
  background-color: rgb(192, 192, 168);
 
}
.td{
  text-align: center;
  border:2px solid black ;
  
}
.AddExpense{
  color:black;
  text-shadow: none;

  /* font-size: large; */
}
.expName{
  font-size: 20px;
}
#ExpName{
  width:30%;
  height: 20px;
  
}
#ExpCost{
  width:20%;
  height: 20px;
}
.saveExpense{
  background-color: rgb(174, 213, 184);
  padding: 5px 15px
}
.span_exp{
  font-size: larger;
  
}